.navbar {
  border-bottom: 1pt solid #efefef
}

.promoBanner.active {
  background-color: #437d6d;
  background-image: linear-gradient(45deg, #009688, #4db6ac)
}

.navbar.transparent {
  border-bottom: 0
}

.navbar-dark .navbar-toggler:focus {
  outline: 0;
  border-color: rgba(255, 255, 255, .6)
}

a {
  color: #44caa2
}

a:active,
a:focus,
a:hover {
  text-decoration: none;
  color: #13b181
}

/* -------------------------------------------------------------------------- */

.promo {
  color: #fff;
  text-shadow: 0 3px 12px rgba(0, 0, 0, .2)
}

.promo h1 {
  font-weight: 500
}

.promo h3 a {
  color: inherit;
  border-bottom: 1px dashed currentColor;
  display: inline-block;
  margin-bottom: -1px;
  text-decoration: none;
  transition: text-shadow 300ms ease
}

.promo h3 a:hover {
  text-shadow: 0 4px 6px rgba(0, 0, 0, .4)
}

.promo h3 a:active,
.promo h3 a:hover:active {
  text-shadow: 0 0 0 transparent
}

.promoBanner {
  background-image: url(/images/background.svg), linear-gradient(45deg, #009688, #4db6ac)!important;
  background-repeat: repeat;
  background-size: 3.5rem, 100%;
  clip-path: polygon(0 0, 100% 0, 100% calc(100% - 2rem), 0 100%);
  transition: 0ms background-position ease
}

@media (max-width:576px) {
  .promo>h1 {
    font-size: 3rem
  }
}

/* -------------------------------------------------------------------------- */

.headerTitle {
    background-color: transparent;
    color: #FFFFFF;
    font-weight: 600;
}

.meetTheTeam ul {
    list-style: none;
    padding-left: 0;
}

.meetTheTeam ul img {
    border-radius: 15%;
    height: 48px;
    width: 48px;
}

.meetTheTeam ul {
    list-style: none;
    padding-left: 0;
}

.meetTheTeam ul li {
    margin-bottom: 3px;
}

.meetTheTeam ul li img {
    margin-right: 15px;
    border-radius: 48px;
}

.meetTheTeam ul code {
    background-color: #e83e8c;
    color: #FFFFFF;
    font-family: inherit;
    padding: 0.2rem 0.5rem;
    position: relative;
    margin-left: 6px;
    display: inline-block;
}

.meetTheTeam ul code:before {
    content: '';
    border-right: 3px solid #E83E8C;
    border-bottom: 3px solid transparent;
    border-top: 3px solid transparent;
    position: absolute;
    top: calc(50% - 2px);
    left: -3px;
}

.meetTheTeam h3 {
    margin-bottom: 1rem;
}
